#home{
	position: relative;
}
/*============================= HOME LANDING IMAGE =====================================*/
.home-inner{
	background: url(../../img/bulldozer.jpg);
	&:after{
		content: '';
		display: block;
		position: absolute;
		top: 0;
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, .4)
	}
}
/*============================= HOME CAPTION =====================================*/ 

 .caption{
	 width: 100%;
	 max-width: 100%;
	 position: absolute;
	 top: 38%;
	 z-index: 1;
	 
 }
 #features{
	 width: 100%;
	 height: 100vh;
 }

 .caption h1{
	 font-size: 4.5rem;
	 text-transform: uppercase;
	 font-weight: 700;
	 letter-spacing: .3rem;
	 text-shadow: .1rem .1rem .8rem $dark;
	 padding-bottom: 1rem;
 }
 .caption h3{
	font-size: 2.2rem;
	font-weight: 700;
	//text-transform: uppercase;
	text-shadow: .1rem .1rem .5rem $dark;
	padding-bottom: 2rem;
}
.caption .btn-lg{
	font-size: 1.2rem;
	text-transform: uppercase;
	border-color: $base-white;
	font-weight: 700;
	padding: .8rem 1.5rem;
	//border-width: medium;
	display: inline-block!important;

	&:hover{
		color: $dark;
		background: $base-white
	}
}